</template>

<script>
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data () {
    return {
      histories: [{txt: '1/31/2023 rc 0'}, {txt: '1/30/2023 rc 5'}],
      rc: 0,
      other: 0,
      torn: 0,
      kab: 0
    }
  },
  mounted () {
    this.process_counts()
  },
  methods: {

    process_counts () {
      const counters = ['rc', 'other', 'torn', 'kab']
      this.rc = 0
      this.other = 0
      this.torn = 0
      this.kab = 0
      const today = new Date().toLocaleDateString()
      for (const key in localStorage) {
        for (const x in counters) {
          const counter = counters[x]
          if (key.includes(counter)) {
            const keyBits = key.split('-')
            const dt = keyBits[1]
            const amountToAdd = parseInt(localStorage.getItem(key))
            if (dt.includes(today)) {
              this[counter] += amountToAdd
            } else {
              const historyKey = 'history_' + keyBits[0] + dt
              if (localStorage[historyKey] === undefined) {
                localStorage.setItem(historyKey, 0)
              }
              const thereBefore = parseInt(localStorage.getItem(historyKey))
              const newAmount = thereBefore + amountToAdd
              localStorage.setItem(historyKey, newAmount)
            }
          } else {
            console.log('no')
          }
        }
      }
    },

    countIt (which, typ, amount) {
      const now = new Date().toLocaleString()
      const key = which + '-' + now
      let toSaveDown = 0
      if (typ === 'plus') {
        toSaveDown += amount
      } else if (typ === 'minus') {
        toSaveDown -= amount
      }
      localStorage.setItem(key, toSaveDown)
      this.process_counts()
    }

  }
}
</script>

